<template>

    <div class="footer">
        
        <label for="all-finish">
            <input type="checkbox" id="all-finish" :checked="all_finish" @change="setAllFinish">全選
        </label>

        <button @click="clearFinish">刪除所有已完成項目</button>
    </div>

</template>

<script>
import { ref } from 'vue'

export default {
    name: 'TodoListFooter',

    // 接收自定義事件
    emits: [
        'eventClearFinish',
        'eventSetAllFinish'
    ],

    setup(props, context) {
        // 數據
        let all_finish = ref(false);


        // 方法

        // 全選
        function setAllFinish() {
            all_finish.value = !all_finish.value;

            context.emit('eventSetAllFinish', all_finish.value) // 觸發eventSetAllFinish自定義事件
        }

        // 刪除已完成的項目
        function clearFinish() {
            all_finish.value = false;

            context.emit('eventClearFinish') // 觸發eventClearFinish自定義事件
        }



        return {
            all_finish,

            clearFinish,
            setAllFinish,
        }
    }
}
</script>

