<template>

    <!-- 新增 -->
    <input type="text" placeholder="請輸入項目" v-model="addItemName">
    <button @click="addItem">新增</button>

</template>

<script>
import { ref } from 'vue'

export default {
    name: 'TodoList',

    // 接收自定義事件
    emits: [
        'eventAddItem'
    ],


    setup(props, context) {
        // 數據
        let addItemName = ref('');


        // 方法

        // 新增
        function addItem () {
            // 新項目
            let obj = {};
            obj.name = addItemName.value; // 項目名稱
            obj.isFinish = false; // 項目狀態
            obj.tempName = obj.name; // 編輯模式名稱
            obj.editStatus = false; // 編輯模式狀態
            
            addItemName.value = ''; // 清空輸入框
            
            context.emit('eventAddItem', obj) // 觸發eventAddItem自定義事件
        }


        return {
            addItemName,

            addItem,
        }
    }
}
</script>

