<template>

    <ul class="list-wrap">
        <li v-for="(t, index) of fromData.todos" :key="index">

            <!-- 一般模式 -->
            <div v-if="!t.editStatus">
                <input type="checkbox" :id="'item'+index" :checked="t.isFinish" @change="updateItemStatus(index)">
                <label :for="'item'+index">{{ t.name }}</label>

                <button @click="enableEditItem(index)">編輯</button>
                <button @click="delItem(index)">刪除</button>
            </div>

            <!-- 編輯模式 -->
            <div v-if="t.editStatus">
                <input type="text" v-model="t.tempName">
                <button @click="disableEditItem(index)">取消</button>
                <button @click="editItem(index)">確定</button>
            </div>
        </li>
    </ul>

    

</template>

<script>
import { toRef } from 'vue'

export default {
    name: 'TodoListBody',

    // 接收數據
    props: ['data'],

    // 接收自定義事件
    emits: [
        'eventUpdateStatus',
        'eventDelItem',
        'eventEnableEditItem',
        'eventDisableEditItem',
        'eventEditItem'
    ],


    setup(props, context) {
        // 數據

        // Props gotten directly from vue 3 setup are not reactive
        // https://stackoverflow.com/questions/69855836/props-gotten-directly-from-vue-3-setup-are-not-reactive
        let fromData = toRef(props, 'data')


        // 方法

        // 修改項目狀態
        function updateItemStatus(index) {
            context.emit('eventUpdateStatus', index) // 觸發eventUpdateStatus自定義事件
        }

        // 刪除項目
        function delItem(index) {
            context.emit('eventDelItem', index) // 觸發eventDelItem自定義事件
        }


        // 編輯: 開啟編輯模式
        function enableEditItem(index) {
            context.emit('eventEnableEditItem', index) // 觸發eventEnableEditItem自定義事件
        }
        // 取消: 關閉編輯模式
        function disableEditItem(index) {
            context.emit('eventDisableEditItem', index) // 觸發eventDisableEditItem自定義事件
        }

        // 確定: 編輯項目
        function editItem(index) {
            context.emit('eventEditItem', index) // 觸發eventEditItem自定義事件
        }
        

        return {
            // initData,
            fromData,

            updateItemStatus,
            delItem,
            enableEditItem,
            disableEditItem,
            editItem,
        }
    }
}
</script>

