<template>
    <div class="todolist">
        <h2>Todolist子組件</h2>

        <!-- Header -->
        <TodoListHeader
            @eventAddItem="addItem"
        />

        <hr>

        <!-- Body -->
        <TodoListBody :data="data"
            @eventUpdateStatus="updateItemStatus" 
            @eventDelItem="delItem"
            @eventEnableEditItem="enableEditItem"
            @eventDisableEditItem="disableEditItem"
            @eventEditItem="editItem"
        />

        <hr>

        <!-- Footer -->
        <TodoListFooter
            @eventClearFinish="clearFinish"
            @eventSetAllFinish="setAllFinish"
        />
        

        <blockquote>
            <pre>{{ data }}</pre>
        </blockquote>
        
        
    </div>
</template>

<script>
import { reactive } from 'vue'

import TodoListHeader from './TodoListHeader.vue'
import TodoListBody from './TodoListBody.vue'
import TodoListFooter from './TodoListFooter.vue'


export default {
    name: 'TodoList',
    components: { 
        TodoListBody, 
        TodoListHeader, 
        TodoListFooter 
    },

    setup() {
        // 數據
        const data = reactive({
            // 待辦事項
            todos: [
                { name: '吃飯', isFinish: false, tempName: '吃飯', editStatus: false },
                { name: '睡覺', isFinish: false, tempName: '睡覺', editStatus: false },
            ]
        })


        // 方法

        // 新增項目
        function addItem(obj) {
            data.todos.push(obj); // 將物件加入陣列
        }

        // 刪除項目
        function delItem(index) {
            data.todos.splice(index, 1);
        }

        // 更新項目狀態
        function updateItemStatus(index) {
            data.todos[index].isFinish = !data.todos[index].isFinish;
        }

        // 全選
        function setAllFinish(value) {
            // 遍歷全部項目
            data.todos.forEach(item => {
                item.isFinish = value
            });
        }

        // 刪除所有已完成項目
        function clearFinish () {
            // reactive array 刪除多個符合條件的元素
            // for循环，从后往前删: https://segmentfault.com/q/1010000043536361
            for ( let i=(data.todos.length - 1) ; i>=0 ; i-- ) {
                if ( data.todos[i].isFinish ) {
                    data.todos.splice(i, 1)
                }
            }
        }


        // // 編輯: 開啟編輯模式
        function enableEditItem(index) {
            data.todos[index].editStatus = true; // 開啟編輯模式
        }
        // 取消: 關閉編輯模式
        function disableEditItem(index) {
            data.todos[index].tempName = data.todos[index].name; // 還原編輯輸入框
            data.todos[index].editStatus = false; // 關閉編輯模式
        }
        // 確定: 編輯項目
        function editItem(index) {
            data.todos[index].name = data.todos[index].tempName; // 修改名稱
            data.todos[index].editStatus = false; // 關閉編輯模式
        }


        return {
            data,

            addItem,
            delItem,
            updateItemStatus,

            clearFinish,
            setAllFinish,

            enableEditItem,
            disableEditItem,
            editItem,
        }
    }
}
</script>
